export default function BackgroundGrid() {
  return (
    <div aria-hidden className="pointer-events-none absolute inset-0">
      <div className="absolute inset-0 bg-[radial-gradient(1000px_600px_at_80%_-20%,rgba(34,211,238,0.18),transparent_70%),radial-gradient(800px_500px_at_20%_120%,rgba(99,102,241,0.18),transparent_70%)]" />
      <div className="absolute inset-0 opacity-[0.07] bg-[linear-gradient(to_right,transparent_0,rgba(255,255,255,0.4)_1px,transparent_1px),linear-gradient(to_bottom,transparent_0,rgba(255,255,255,0.4)_1px,transparent_1px)] bg-[size:60px_60px]" />
    </div>
  )
}
